/* Map styles */
#map {
    height: 100%;
    width: 100%;
   
   }
   
   .mapContainer{
     height: 80vh;
     width: 100%;
     /* grid-area: "sideBar Map"; */
   
   }
   /* info window */
   .vName, .lName{
     font-size: 20px;
     font-weight: 800;
   
   }
   
   .vCat, .lCat{
     font-size: 18px;
     font-weight: 700;
   }
   
   .vAdd, .lAdd{
     font-size: 16px;
     font-weight: 600;
   }
   
   

.searchCont{
    width:80%;
    height: 40px;
    position: -webkit-sticky;
    position: sticky; 
    font-size: 16px;
    top:30px;
    margin: 0 0 30px 0;  
  }
  
  .side-bar{
    width: 0;
    height:80vh;
    background-color: #2F2FA2;
    position: absolute;
    z-index: 100;
    left: 0;
    overflow: hidden;
    transition: 0.5s; 
    overflow: scroll;
  }

  .openSide{
    width:50vw;
  }
  
  .search-results{
    width: 100%;                
  }
  
  .listCont{
    list-style: none;
    padding: 0%;
    width: 90%;
    padding: 10px;
    margin: auto;
  }

  .listItem{
    background-color:#aadaff;
    align-self: center;
    margin: 0%;
  }

  .listItem :hover{
    background-color: #f64c71;
    color:whitesmoke;
    cursor: pointer;
  
  }

  .locationCont{
    align-self: center;
  }
  
  .list-container {
    width: 100%;
    height: 90%;
    overflow-y: scroll
  }
  
  .skip-link {
    display: block;
    position: relative;
    background: black;
    color: white; 
    z-index: 100;
    left:-1000px;
  }
  
  .skip-link:focus {
    left: 0px;  
  }       

.App {
  text-align: center;
  
}
