/* Media Queries */

@media screen and (min-width: 500px) {
  .pageHeader h1 {
    font-size: 55px;
    padding: 0 0 0 60px;
  }
  .openSide{
    width: 40vw;
  }
}

@media screen and (min-width: 800px) {
  .pageHeader h1 {
    font-size: 58px;
    
  }

  .openSide{
    width: 35vw;
  }

  .googleLogo{
    width: 70px;
     height: 70px;
     
   }

   .fourSq{
    width: 421px;
  }

}

@media screen and (min-width: 1000px) {
  .sidebarIconToggle{
    visibility: hidden;
  }

  .side-bar{
    width: 30vw;
    overflow-x: hidden;
  }

}




/* HAmburger Menu */

#openSideBarMenu {
    position: absolute;
    top: 0;
    right: 25px;
    font-size: 36px;
    margin-left: 50px; 
    z-index: 1000;
  }
  
  .sidebarIconToggle {/* app */
    transition: all 0.3s;
    box-sizing: border-box;
    cursor: pointer;
    position: absolute;
    z-index: 1000;
    height: 100%;
    width: 100%;
    top: 22px;
    left: 15px;
    height: 50px;
    width: 50px;
  }
  .sidebarIconToggle:focus {
    outline: 3px solid black;
  }
  
  
  .spinner {
      transition: all 0.3s;
      box-sizing: border-box;
      position: absolute;
      height: 3px;
      width: 100%;
      background-color: #fff;
      padding: 2px;
  }
  .horizontal {
      transition: all 0.3s;
      box-sizing: border-box;
      position: relative;
      float: left;
      margin-top: 5px;
  }
  .diagonal.part-1 {
      position: relative;
      transition: all 0.3s;
      box-sizing: border-box;
      float: left;
      padding: 2px;
  }
  .diagonal.part-2 {
      transition: all 0.3s;
      box-sizing: border-box;
      position: relative;
      float: left;
      margin-top: 3px;
      padding: 2px;
  }
  input[type=checkbox]:checked ~ .sidebarIconToggle > .horizontal {
      transition: all 0.3s;
      box-sizing: border-box;
      opacity: 0;
  }
  input[type=checkbox]:checked ~ .sidebarIconToggle > .diagonal.part-1 {
      transition: all 0.3s;
      box-sizing: border-box;
      -webkit-transform: rotate(135deg);
              transform: rotate(135deg);
      margin-top: 8px;
  }
  input[type=checkbox]:checked ~ .sidebarIconToggle > .diagonal.part-2 {
      transition: all 0.3s;
      box-sizing: border-box;
      -webkit-transform: rotate(-135deg);
              transform: rotate(-135deg);
      margin-top: -9px;
  }
    
 input[type=checkbox] {
    transition: all 0.3s;
    box-sizing: border-box;
    display: none;
} 
  


